___CSS_LOADER_EXPORT___.push([module.id, `footer {
    grid-area: footer;
    display: grid;
    grid-auto-flow: column;
    justify-content: space-between;
    align-items: end; 
    gap: 10px;
}

footer form {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(7, 50px);
    gap: 30px;
    grid-auto-flow: column;
    align-items: center;
    justify-items: center;
    padding-top: 60px;
}

footer > button {
    border: 0;
    border-radius: 14px;
    padding: 10px;
    width: 200px;
    color: blue;
    display: grid;
    align-items: center;
    justify-items: start;
    gap: 5px;
}

button#new-reminder {
    grid-template-columns: max-content 1fr;

}

button#openListModal {
    height: 70px;
    justify-items: center;
}

/* REMINDER MODAL */

#reminder-modal {
    /* display: grid; */

}

#reminder-modal .form-wrapper {
    min-height: 100vh;
}

#reminder-modal form {
    grid-template-areas: 'title title title title'
                         'notes notes notes notes' 
                         '. date date .' 
                         '. time time .' 
                         '. priority priority .'
                         'list list list list' 
                         '. done reset .';
}

#reminder-modal label:first-of-type {
    grid-area: title;
}

#reminder-modal label:nth-of-type(2) {
    grid-area: notes;
}

#reminder-modal label:nth-of-type(3) {
    grid-area: date;
}

#reminder-modal label:nth-of-type(4) {
    grid-area: time;
}

#reminder-modal label:nth-of-type(5) {
    grid-area: priority;
}

#reminder-modal label:nth-of-type(6) {
    grid-area: list;
}

#reminder-modal input {
    width: 150px;
}

#reminder-modal button:first-of-type {
    grid-area: done;
}

#reminder-modal button:last-of-type {
    grid-area: reset;
}

/* LIST MODAL */

#list-modal form {
    grid-template-areas: '. input input .' 
                         '. color color .' 
                         '. done reset .';
}

#list-modal input:first-of-type {
    width: 250px;
    grid-area: input;
}

#list-modal input:nth-of-type(2) {
    grid-area: color;
}

#list-modal button:first-of-type {
    grid-area: done;
}

#list-modal button:last-of-type {
    grid-area: reset;
